import Mock from 'mockjs';

export function checkboxMockData() {
  return Mock.mock({
    "code": 0,
    "result_data|2-6": [
      {
        "label": "@city()",
        "value|+1": 1
      }
    ]
  })
}

export function radioMockData() {
  return Mock.mock({
    "code": 0,
    "result_data|2-4": [
      {
        "label": "@city()",
        "value|+1": 1
      }
    ]
  })
}

export function selectMockData() {
  return Mock.mock({
    "code": 0,
    "result_data|2-6": [
      {
        "label": "@province()",
        "value|+1": 1
      }
    ]
  })
}

